class QNA extends HTMLElement {
    connectedCallback() {
        this.render();
    }

    render() {
        this.innerHTML = `
        <section class="qna-section" id= "question">
            <div class="text-head">
                <div class="mini-text">PERTANYAAN YANG SERING DITEMUI</div>
                <h2>Pertanyaan? <span>Jawaban.</span></h2>
            </div>
            <div class="main-content">
                <article class="content-item" tabindex="0">
                    <div class="question ">
                        <h3>Apa itu Jaksara</h3>
                        <svg xmlns="http://www.w3.org/2000/svg " id="arrow " x="0 " y="0 " version="1.1 " viewBox="0 0 29 29 " xml:space="preserve ">
                        <path fill="none " stroke="#000 " stroke-linecap="round " stroke-linejoin="round " stroke-miterlimit="10 " stroke-width="2 " d="m20.5 11.5-6 6-6-6 "></path>
                        </svg>
                    </div>
                    <div class="answer">
                        <p>
                            Jaksara adalah sebuah web app yang dibangun untuk membantu orang untuk bisa mempelajari bahasa jawa dengan mudah dan efektif, selain itu juga untuk melestarikan kebudayaan bahasa daerah agar tidak punah dan terus berkembang.
                        </p>
                    </div>
                </article>
                <article class="content-item" tabindex="0">
                    <div class="question">
                        <h3>Apakah ini gratis?</h3>
                        <svg xmlns="http://www.w3.org/2000/svg " id="arrow " x="0 " y="0 " version="1.1 " viewBox="0 0 29 29" xml:space="preserve ">
                        <path fill="none" stroke="#000 " stroke-linecap="round " stroke-linejoin="round " stroke-miterlimit="10 " stroke-width="2 " d="m20.5 11.5-6 6-6-6 "></path>
                        </svg>
                    </div>
                    <div class="answer">
                        <p>
                            Iya, ini gratis khusus untuk kamu yang ingin mempelajari bahasa jawa dan ingin melestarikan bahasa daerah terutama jawa
                        </p>
                    </div>
                </article>
                <article class="content-item" tabindex="0">
                    <div class="question">
                        <h3>Fitur utamanya apa?</h3>
                        <svg xmlns="http://www.w3.org/2000/svg " id="arrow " x="0 " y="0 " version="1.1 " viewBox="0 0 29 29 " xml:space="preserve ">
                        <path fill="none " stroke="#000 " stroke-linecap="round " stroke-linejoin="round " stroke-miterlimit="10 " stroke-width="2 " d="m20.5 11.5-6 6-6-6 "></path>
                        </svg>
                    </div>
                    <div class="answer">
                        <p>
                            Kami menyediakan dua fitur utama yaitu materi untuk belajar aksara jawa dan bermain kuis untuk melatih pemahaman anda dalam mempelajari bahasa jawa, ini akan membuat belajar lebih cepat dan mudah dipahami.
                        </p>
                    </div>
                </article>
                <article class="content-item" tabindex="0">
                    <div class="question">
                        <h3>Jika ada kendala, aku harus apa?</h3>
                        <svg xmlns="http://www.w3.org/2000/svg " id="arrow " x="0 " y="0 " version="1.1 " viewBox="0 0 29 29 " xml:space="preserve">
                        <path fill="none " stroke="#000 " stroke-linecap="round " stroke-linejoin="round " stroke-miterlimit="10 " stroke-width="2 " d="m20.5 11.5-6 6-6-6 "></path>
                        </svg>
                    </div>
                    <div class="answer">
                        <p>
                            Kamu bisa mengirim email atau no telpo yang kami berikan, kami akan merespon kapanpun walau hari libur.
                        </p>
                    </div>
                </article>
                <article class="content-item" tabindex="0">
                    <div class="question">
                        <h3>Apakah ada komunitas?</h3>
                        <svg xmlns="http://www.w3.org/2000/svg" id="arrow " x="0 " y="0 " version="1.1 " viewBox="0 0 29 29 " xml:space="preserve">
                        <path fill="none " stroke="#000 " stroke-linecap="round " stroke-linejoin="round " stroke-miterlimit="10 " stroke-width="2 " d="m20.5 11.5-6 6-6-6 "></path>
                        </svg>
                    </div>
                    <div class="answer">
                        <p>
                            Iya tentunya ada, kami memiliki komunitas dimana berisi orang-orang yang memiliki tujuan belajar yang berbeda-beda seperti tujuan menguasai bahasa jawa agar lancar komunikasinya atau untuk melestarikan budaya bahsa daerah
                        </p>
                    </div>
                </article>
                <article class="content-item" tabindex="0">
                    <div class="question">
                        <h3>Apa ada sertifikat?</h3>
                        <svg xmlns="http://www.w3.org/2000/svg " id="arrow " x="0 " y="0 " version="1.1 " viewBox="0 0 29 29 " xml:space="preserve">
                        <path fill="none " stroke="#000 " stroke-linecap="round " stroke-linejoin="round " stroke-miterlimit="10 " stroke-width="2 " d="m20.5 11.5-6 6-6-6 "></path>
                        </svg>
                    </div>
                    <div class="answer">
                        <p>
                            Maaf, saat ini kami tidak memberikan sertifikat kepada anda, tetapi nanti mungkin akan kami pertimbangkan untuk menambah motivasi belajar anda.
                        </p>
                    </div>
                </article>
                <article class="content-item" tabindex="0">
                    <div class="question">
                        <h3>Apakah materinya mudah?</h3>
                        <svg xmlns="http://www.w3.org/2000/svg " id="arrow " x="0 " y="0 " version="1.1 " viewBox="0 0 29 29 " xml:space="preserve">
                        <path fill="none " stroke="#000 " stroke-linecap="round " stroke-linejoin="round " stroke-miterlimit="10 " stroke-width="2 " d="m20.5 11.5-6 6-6-6 "></path>
                        </svg>
                    </div>
                    <div class="answer">
                        <p>
                            Iya, materi yang kami berikan sangat mudah karena hanya teridiri dari dasar dari aksara jawa, yang seharusnya anda bisa mudah memahaminya. 
                        </p>
                    </div>
                </article>
            </div>
        </section>
        `;
    }
}

customElements.define('qna-content', QNA);